import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, Platform, ViewController, IonicApp, AlertController  } from 'ionic-angular';
import { APIService } from '../../services/api_service';
import { UtilService } from '../../services/util_service';
import { ModalController } from 'ionic-angular';

/**
 * Generated class for the LoadingsecondPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-loadingsecond',
  templateUrl: 'loadingsecond.html',
})
export class LoadingsecondPage {
  remainingTime
  time 
  runTimer
  hasStarted 
  hasFinished 
  timeInSeconds
  displayTime
  order_id
  payment_id
  paymentstatus
  Total
  amount
  email
  customer_name
  mobile_number
  order_ids
  payment_ids
  token
  id
  renew
  type
  fifteensecond
  models
  constructor(public navCtrl: NavController,
    public navParams: NavParams,
    private apiService: APIService,
    private util: UtilService,
    public viewCtrl: ViewController,
    public atrCtrl: AlertController,
    public modalCtrl: ModalController,
    public platform: Platform,
    private ionicApp: IonicApp) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad LoadingsecondPage');
  }
  ngOnInit()
  {
    this.initTimer();
    this. startTimer();
  }
  initTimer() {
    // Pomodoro is usually for 25 minutes
    if (!this.timeInSeconds) { 
      this.timeInSeconds = 1800; 
    } 
    this.time = this.timeInSeconds;    
    this.runTimer = false;
    this.hasStarted = false;
    this.hasFinished = false;
    this.remainingTime = this.timeInSeconds;
    
    this.displayTime = this.getSecondsAsDigitalClock(this.remainingTime);
    console.log(this.displayTime);
  }
 
  startTimer() {
    this.runTimer = true;
    this.hasStarted = true;
    this.timerTick();
  }
 
  pauseTimer() {
    this.runTimer = false;
  }
  
  resumeTimer() {
    this.startTimer();
  }
 
  timerTick() {
   setTimeout(() => {
 
    if (!this.runTimer) { return; }
    this.remainingTime--;
    console.log(this.remainingTime)
    this.displayTime = this.getSecondsAsDigitalClock(this.remainingTime);
    
    if(this.remainingTime==1797)
    {
      this.buttonhide();
    }
    
    else if(this.remainingTime==1780)
    {
      this.order_id =  localStorage.getItem('customer_order_payement_id');
      this.payment_id =  "";
      this.paymentstatus =  "";
      this.apiService.statuscheck(this.order_id,this.payment_id,this.paymentstatus).then((res) => {
        if(res['success'])
        {
          this.pauseTimer();
          localStorage.setItem('customer_payement_status', 'success');
          this.navCtrl.setRoot('PaymentsuccessfulPage');
        }
        else if(res['success']=='inprogress')
        {
          this.fifteensecond = this.remainingTime -15;
        }
        else{
          this.pauseTimer();
          this.navCtrl.setRoot('PaymentfailedPage');
        }
          
      }).catch((err) => {
        // this.navCtrl.setRoot('PaymentfailedPage');
      });    
    }
    else if(this.remainingTime==this.fifteensecond)
    {
      this.order_id =  localStorage.getItem('customer_order_payement_id');
      this.payment_id =  "";
      this.paymentstatus =  "";
      this.apiService.statuscheck(this.order_id,this.payment_id,this.paymentstatus).then((res) => {
        if(res['success']==true)
        {
          this.pauseTimer();
          localStorage.setItem('customer_payement_status', 'success');
          this.navCtrl.setRoot('PaymentsuccessfulPage');
        }
        else if(res['success']=='inprogress')
        {
          this.fifteensecond = this.remainingTime -15;
        }
        else{
          this.pauseTimer();
          this.navCtrl.setRoot('PaymentfailedPage');
        }
          
      }).catch((err) => {
        // this.navCtrl.setRoot('PaymentfailedPage');
      });    
    }
    else if(this.remainingTime==0)
    {
      this.pauseTimer();
      // if (window.history.state.modal) {
        history.back();
      // }
      // document.getElementById('modal-close').click();
      // this.model.dismiss();
      
      this.navCtrl.setRoot('CheckoutPage');
    }
    console.log(this.displayTime)
    if (this.remainingTime > 0) {
    this.timerTick();
    }
    else {
      this.hasFinished = true;
    }
   }, 1000);
  }
 
  getSecondsAsDigitalClock(inputSeconds: number) {
    var sec_num = parseInt(inputSeconds.toString(), 10); // don't forget the second param
    var hours = Math.floor(sec_num / 3600);
    var minutes = Math.floor((sec_num - (hours * 3600)) / 60);
    var seconds = sec_num - (hours * 3600) - (minutes * 60);
    var hoursString = '';
    var minutesString = '';
    var secondsString = '';
    hoursString = (hours < 10) ? "0" + hours : hours.toString();
    minutesString = (minutes < 10) ? "0" + minutes : minutes.toString();
    secondsString = (seconds < 10) ? "0" + seconds : seconds.toString();
    
    return hoursString + ':' + minutesString + ':' + secondsString;
  }

    //rzp_live_AAPrMXQhJP49GU
    //rzp_test_uZ1iWGWR9vDDSM // old //rzp_test_1DP5mmOlF5G5ag //rzp_test_zTdGNuEfmCHtil
    //rzp_test_e1wp9DFMzylCAL // new test key

  buttonhide() {
    this.Total = localStorage.getItem('Total')
    this.amount = String(this.Total);
    this.amount = this.amount.replace('.', '');
    this.email = localStorage.getItem('email');
    this.customer_name = localStorage.getItem('customer_name');
    this.mobile_number = localStorage.getItem('num');
    this.util.hideLoader();
    var options = {
      description: '',
      image: '',
      currency: 'INR',
      key: 'rzp_test_zTdGNuEfmCHtil',
      amount: this.amount,
      name: 'Mummy Meals',
      order_id:localStorage.getItem('customer_order_payement_id'),
      prefill: {
          email: this.email,
          contact: this.mobile_number,
          name: this.customer_name
      },
      theme: {
          color: '#f8b13f'
      },
     modal: {
          ondismiss: function () {        
              console.log('innnnnnnnnnnnnn');            
          }
      }
    };
    var successCallback = (payment_id) => {
        this.util.showLoader();
        this.pauseTimer();
        localStorage.setItem('customer_payement_status', 'success');
        localStorage.setItem('razorpayment_id', payment_id);
        this.order_ids =  localStorage.getItem('customer_order_payement_id');
        this.payment_ids =  localStorage.getItem('razorpayment_id');
        this.paymentstatus =  "success";
        this.apiService.statuscheck(this.order_ids,this.payment_ids,this.paymentstatus).then((res) => {
          this.util.hideLoader();
            if(res['success'])
            {
                localStorage.setItem('successwait', 'yes');
                document.getElementById('buttonhidden').click();
            }
            else{
                localStorage.setItem('successwait', 'no');
                document.getElementById('buttonhidden').click();
            }
              
          }).catch((err) => {
            localStorage.setItem('successwait', 'no');
            document.getElementById('buttonhidden').click();
          });
          localStorage.setItem('successwait', 'yes');
          document.getElementById('buttonhidden').click();
    };
    var cancelCallback = (error) => {
      this.util.showLoader();
      this.pauseTimer(); 
      this.token = localStorage.getItem('token');
      this.id = localStorage.getItem('id');
      this.payment_id = localStorage.getItem('payment_id');
      if (localStorage.getItem('renewis') == '1') {
          this.renew = 1
          this.type = 'renew';
          localStorage.setItem('failedstatus',this.type);
      }
      else {
          this.renew = 0
          this.type = 'regular';
          localStorage.setItem('failedstatus',this.type);
      }
      //

      this.order_ids =  localStorage.getItem('customer_order_payement_id');
      this.payment_ids =  "";
      this.paymentstatus =  "failed";
      this.apiService.statuscheck(this.order_ids,this.payment_ids,this.paymentstatus).then((res) => {
        this.util.hideLoader();
          if(res['success'])
          {
              localStorage.setItem('successwait', 'no');
              document.getElementById('buttonhidden').click();
          }
          else{
              localStorage.setItem('successwait', 'no');
              document.getElementById('buttonhidden').click();
          }
            
        }).catch((err) => {
          localStorage.setItem('successwait', 'no');
          document.getElementById('buttonhidden').click();
        });
        console.log(localStorage.getItem('successwait'));
      localStorage.setItem('successwait', 'no');
      console.log(localStorage.getItem('successwait'));
      document.getElementById('buttonhidden').click();
      console.log(localStorage.getItem('successwait'));
    };
    console.log(localStorage.getItem('successwait'));
  

    RazorpayCheckout.open(options, successCallback.bind(this), cancelCallback.bind(this));
  
  } 
    // this.initTimer();
    // this. startTimer();
  buttonhidepage()
  {
    if(localStorage.getItem('successwait')=='yes')
    {
      this.navCtrl.setRoot('PaymentsuccessfulPage');
    }
    else if(localStorage.getItem('successwait')=='no'){
        this.navCtrl.setRoot('PaymentfailedPage');
    }
  }

}



